import React, {Component} from 'react';


export default class RODOCookis extends Component {

        render()
        {
            return (
                <div>
                    <h5 style={{textAlign:"center"}}>Polityka Cookies</h5>

                    <p>
                        Ponisza
                        Polityka
                        Cookies
                        okrela
                        zasady
                        zapisywania
                        i
                        uzyskiwania
                        dostpu
                        do
                        danych
                        na
                        Urzdzeniach
                        Uytkownikw
                        korzystajcych
                        z
                        Serwisu
                        do
                        celw
                        wiadczenia
                        usug
                        drog
                        elektroniczn
                        przez
                        Administratora
                        Serwisu
                        .</p>

                    <h6 style={{textAlign:"center"}}> 1 Definicje</h6>
                    <ul>
                        <li><p><strong>Serwis</strong> - serwis internetowy dziaajcy pod adresem <span
                            id="serwis"><span
                            className="colored">https://your-gym.herokuapp.com/login</span></span></p></li>
                        <li><p><strong>Serwis zewntrzny</strong> - serwis internetowe partnerw, usugodawcw lub
                            usugobiorcw Administratora</p></li>
                        <li><p><strong>Administrator</strong> - firma <span id="firma"><span
                            className="colored">your-gym</span>, prowadzca dziaalno pod adresem: <span
                            className="colored">Worcaw</span>, o nadanym numerze identyfikacji podatkowej (NIP): <span
                            className="colored">123456789</span></span>, wiadczca usugi drog elektroniczn za
                            porednictwem Serwisu oraz przechowujca i uzyskujca dostp do informacji w urzdzeniach
                            Uytkownika</p></li>
                        <li><p><strong>Uytkownik</strong> - osba fizyczna, dla ktrej Administrator wiadczy usugi
                            drog
                            elektroniczna za porednictwem Serwisu.</p></li>
                        <li><p><strong>Urzdzenie</strong> - elektroniczne urzdzenie wraz z oprogramowaniem, za
                            porednictwem, ktrego Uytkownik uzyskuje dostp do Serwisu</p></li>
                        <li><p><strong>Cookies (ciasteczka)</strong> - dane tekstowe gromadzone w formie plikw
                            zamieszczanych na Urzdzeniu Uytkownika</p></li>
                    </ul>
                    <h6 style={{textAlign:"center"}}> 2 Rodzaje Cookies</h6>
                    <ul>
                        <li><p><strong>Cookies wewntrzne</strong> - pliki zamieszczane i odczytywane z Urzdzenia
                            Uytkownika przes system teleinformatyczny Serwisu</p></li>
                        <li><p><strong>Cookies zewntrzne</strong> - pliki zamieszczane i odczytywane z Urzdzenia
                            Uytkownika przez systemy teleinformatyczne Serwisw zewntrznych</p></li>
                        <li><p><strong>Cookies sesyjne</strong> - pliki zamieszczane i odczytywane z Urzdzenia
                            Uytkownika
                            przez Serwis <span>lub Serwisy zewntrzne</span> podczas
                            jednej
                            sesji danego Urzdzenia. Po zakoczeniu sesji pliki s usuwane z Urzdzenia Uytkownika.</p>
                        </li>
                        <li><p><strong>Cookies trwae</strong> - pliki zamieszczane i odczytywane z Urzdzenia
                            Uytkownika
                            przez Serwis <span id="sz2" >lub Serwisy zewntrzne</span> do momentu
                            ich
                            rcznego usunicia. Pliki nie s usuwane automatycznie po zakoczeniu sesji Urzdzenia chyba
                            e
                            konfiguracja Urzdzenia Uytkownika jest ustawiona na tryb usuwanie plikw Cookie po
                            zakoczeniu
                            sesji Urzdzenia.</p></li>
                    </ul>

                    <h6 style={{textAlign:"center"}}> 3 Bezpieczestwo</h6>
                    <ul>
                        <li><p><strong>Mechanizmy skadowania i odczytu</strong> - Mechanizmy skadowania i odczytu
                            Cookies
                            nie pozwalaj na pobierania jakichkolwiek danych osobowych ani adnych informacji poufnych z
                            Urzdzenia Uytkownika. Przeniesienie na Urzdzenie Uytkownika wirusw, koni trojaskich
                            oraz
                            innych robakw jest praktynie niemoliwe.</p></li>
                        <li><p><strong>Cookie wewntrzne</strong> - zastosowane przez Administratora Cookie wewntrzne
                            s
                            bezpieczne dla Urzdze Uytkownikw</p></li>
                        <li><p><strong>Cookie zewntrzne</strong> - za bezpieczestwo plikw Cookie pochodzcych od
                            partnerw Serwisu Administrator nie ponosi odpowiedzialnoci. Lista partnerw zamieszczona
                            jest
                            w dalszej czci Polityki Cookie.</p></li>
                    </ul>

                    <h6 sttyle={{textAlign:"center"}}> 4 Cele do ktrych wykorzystywane s pliki Cookie</h6>
                    <ul id="cele">
                        <li><p><strong><span
                            className="colored">Usprawnienie i uatwienie dostpu do Serwisu</span></strong> -
                            Administrator
                            moe przechowywa w plikach Cookie informacje o prefernecjach i ustawieniach uytkownika
                            dotyczcych Serwisu aby usprawni, polepszy i przypieszy wiadczenie usug w ramach
                            Serwisu.
                        </p></li>
                        <li><p><strong><span className="colored">Logowanie</span></strong> - Administrator wykorzystuje
                            pliki Cookie do celw logowania Uytkownikw w Serwisie</p></li>
                    </ul>
                    <h6 style={{textAlign:"center"}}> 5 Serwisy zewntrzne</h6>
                    <p id="zewinfo"><span className="colored">Administrator nie wsppracuje z serwisami zewntrznymi i Serwis nie umieszcza ani nie korzysta z adnych plikw zewntrznych plikw Cookie.</span>
                    </p>
                    <ul id="zewnetrzne"></ul>
                    <h6 style={{textAlign:"center"}}> 6 Moliwoci okrelania warunkw przechowywania i uzyskiwania
                        dostpu na
                        Urzdzeniach Uytkownika przez Serwis<span id="sz6"
                                                                  > i Serwisy zewntrzne</span>
                    </h6>
                    <ul>
                        <li><p>Uytkownik moe w dowolnym momencie, samodzielnie zmieni ustawienia dotyczce
                            zapisywania,
                            usuwania oraz dostpu do danych zapisanych plikw Cookies</p></li>
                        <li><p>Informacje o sposobie wyczenia plikw Cookie w najpopularniejszych przegldarkach
                            komputerowych i urzdze mobilnych dostpna s na stronie: <a
                                href="http://jakwylaczyccookie.pl">jak wyczy cookie</a>.</p></li>
                        <li><p>Uytkownik moe w dowolnym momencie usun wszelkie zapisane do tej pory pliki Cookie
                            korzystajc z narzdzi Urzdzenia Uytkownika za porednictwem ktrego Uytkowanik korzysta
                            z
                            usug Serwisu.</p></li>
                    </ul>
                    <h6 style={{textAlign:"center"}}> 7 Wymagania Serwisu</h6>
                    <ul>
                        <li><p>Ograniczenie zapisu i dostpu do plikw Cookie na Urzdzeniu Uytkownika moe spowodowa
                            nieprawidowe dziaanie niektrych funkcji Serwisu.</p></li>
                        <li><p>Administrator nie ponosi adnej odpowiedzialnoci za nieprawidowo dziaajce funkcje
                            Serwisu
                            w przypadku gdy Uytkownik ograniczy w jakikolwiek sposb moliwo zapisywania i odczytu
                            plikw
                            Cookie.</p></li>
                    </ul>
                    <h6 style={{textAlign:"center"}}> 8 Zmiany w Polityce Cookie</h6>
                    <ul>
                        <li><p>Administrator zastrzega sobie prawo do dowolnej zmiany niniejszej Polityki Cookie bez
                            koniecznoci informowania o tym uytkownikw.</p></li>
                        <li><p>Wprowadzone zmiany w Polityce Cookie zawsze bd publikowane na tej stronie.</p></li>
                        <li><p>Wprowadzone zmiany wchodz w ycie w dniu publikacji Polityki Cookie.</p></li>
                    </ul>
                </div>
            );
        }
    }





